<template>
  <div>
    <iframe
      class="bimHeight"
      :src="onlineUrl+params"
      style
      frameborder="0"
    ></iframe>
  </div>
</template>
<script>
import Url from "../../../static/js/requestUrl";
export default {
  data() {
    return {
      onlineUrl: "http://47.112.16.168:",
      params:""
    };
  },
  mounted(){
   this.params = this.$route.query.params;
  }
};
</script>
<style scoped>
.bimHeight{
    height: 100%;
}
</style>


